import {
  AiOutlineVerticalAlignTop as IconTop,
  AiOutlineVerticalAlignMiddle as IconCenter,
  AiOutlineVerticalAlignBottom as IconBottom,
  AiOutlineQuestionCircle as IconFallback,
} from 'react-icons/ai';

// Symbols Definitions: begin
enum ESymbols {
  THREEXBAR = 1,
  BAR = 2,
  TWOXBAR = 3,
  SEVEN = 4,
  CHERRY = 5,
}

const symbolsKeysAsArrayOfString = Object.keys(ESymbols).splice(Math.floor(Object.keys(ESymbols).length / 2));
const symbolsValuesAsArrayOfNumber = Object.keys(ESymbols).splice(0, Math.floor(Object.keys(ESymbols).length / 2)).map(x => +x);

const mapSymbolsImagesAsSrc = new Map<number, string>([
  [ESymbols.THREEXBAR, `data:image/png;base64,`],
  [ESymbols.BAR, `data:image/png;base64,`],
  [ESymbols.TWOXBAR, `data:image/png;base64,`],
  [ESymbols.SEVEN, `data:image/png;base64,`],
  [ESymbols.CHERRY, `data:image/png;base64,`],
]);

const getSymbolsImagesAsSrc = (enumeration: number): string => {
  return mapSymbolsImagesAsSrc.get(enumeration) || '';
};
// Symbols Definitions: end

// Symbols Positions: begin
enum ESymbolsPositions {
  TOP = 1,
  CENTER = 2,
  BOTTOM = 3,
}

const symbolsPositionsKeysAsArrayOfString = Object.keys(ESymbolsPositions).splice(Math.floor(Object.keys(ESymbolsPositions).length / 2));
const symbolsPositionsValuesAsArrayOfNumber = Object.keys(ESymbolsPositions).splice(0, Math.floor(Object.keys(ESymbolsPositions).length / 2)).map(x => +x);

const mapSymbolsPositionsAsFC = new Map<number, React.FC>([
  [ESymbolsPositions.TOP, IconTop],
  [ESymbolsPositions.CENTER, IconCenter],
  [ESymbolsPositions.BOTTOM, IconBottom],
]);

const getSymbolsPositionsAsFC = (enumeration: number): React.FC => {
  return mapSymbolsPositionsAsFC.get(enumeration) || IconFallback;
};
// Symbols Positions: end

export {
  ESymbols,
  symbolsKeysAsArrayOfString,
  symbolsValuesAsArrayOfNumber,
  getSymbolsImagesAsSrc,
  ESymbolsPositions,
  symbolsPositionsKeysAsArrayOfString,
  symbolsPositionsValuesAsArrayOfNumber,
  getSymbolsPositionsAsFC,
};
